.projectPage {
  position: relative;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin-top: 20px;
}

.metadata,
.knowledgeBase {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.metadata {
  width: 40%;
}

.knowledgeBase {
  width: 60%;

  .knowledgeBaseList {
    max-height: 200px;
    overflow-y: auto;
  }

  .knowledgeBaseUploadForm {
    position: relative;
    height: 200px;
    padding: 5px;
    border: 5px dashed var(--color-border-main);
    border-radius: 10px;
    overflow-x: hidden;
    overflow-y: auto;

    input {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      background-color: #fff;
    }
  }
}

.headerWrapper {
  display: flex;
  justify-content: space-between;
  background-color: var(--main-background-color-alpha);
}

.buttonsWrapper {
  display: flex;
  gap: 10px;
}

.dragndropMessage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.filesList {
  list-style: none;

  li {
    display: flex;
    justify-content: space-between;
  }
}
